import React, { Component } from 'react';
import { Typography, Box, Grid } from '@material-ui/core';
import me from '../assets/me.png';
import chevronDown from '../assets/chevronDown.png';
import resume from '../assets/resume.pdf';

const styles = {
    // root:{
    //     height:'100vh',
    //     flex: 1,
    //     flexDirection: 'column',
    //     justifyContent: 'center'
    // },
    // main:{
    //     height:'100%',
    // },
    title1:{
        fontFamily:'TwReg',
        fontSize:'6rem',
        letterSpacing: 22,
        lineHeight: 0,
        color:'#272727',
    },
    title2:{
        fontFamily:'TwBold',
        fontSize:'6rem',
        color:'#272727',
    },
    subtitle1:{
        fontFamily:'TwReg', 
        fontSize:'1.25rem', 
        color:'#464646',
        textTransform: 'none'
    },
    subtitle2:{
        fontFamily:'TwItalic', 
        fontSize:'1rem', 
        color:'#727272',
        // lineHeight: 5,
        textDecoration: 'none',
        marginRight: 50
    },
    root: {
        height: '100vh',
    },
    accent: {
        width: '99vw',
        height: '20px',
        backgroundColor: '#ffe400',
        position: 'absolute'
    },
    container: {
        width: '100%',
        height: '100vh',
        display: 'flex',
        flexDirection: 'row',
        justifyContent: 'center',
        alignItems: 'center',
        paddingLeft: '10%',
        paddingRight: '10%'
    },
    text: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
    },
    pic: {
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center'
    },
    me: {
        height: '75%',
        width: '75%'
    },
    down: {
        height: '30px',
        width: '30px',
        position: 'absolute',
        bottom: '40px',
        right: '40px',
        cursor: 'pointer'
    },
    links: {
        width: '30%',
        display: 'flex',
        flexDirection: 'row',
        lineHeight: 5
    }
} 

class Home extends Component {
    
    test(){
        console.log('!')
    }

    render() {
        return (
            // <div style={styles.root}>
            //     <div style={styles.container}>
            //         <Box flexDirection="column" style={styles.main} display="flex" justifyContent="center" alignItems="flex-start">
            //             <Typography style={styles.title}>PAOLO</Typography>
            //             <Typography style={styles.title2}>VELARDE</Typography>
            //             <Typography style={styles.subtitle}>
            //             Software Developer | <br class="mobile-break"></br>University of Toronto</Typography>
            //         </Box>
            //         <div style={styles.imgCont}>

            //         </div>
            //     </div>
            // </div>
            <div style={styles.root}>
                <div style={styles.accent} />
                <Grid container style={styles.container}>
                    <Grid item xs={6} style={styles.text}>
                        <Typography style={styles.title1}>
                            PAOLO{'\n'}
                            <Typography style={styles.title2}>VELARDE</Typography>
                        </Typography>
                        <Typography style={styles.subtitle1}>
                            Software Engineering Intern at KPMG Lighthouse
                        </Typography>
                        <Typography style={styles.subtitle1}>
                            Junior undergrad at University of Toronto
                        </Typography>
                        <Typography style={styles.subtitle1}>
                            Executive and Designer at CS Enrichment Club
                        </Typography>
                        {/* <Typography style={styles.subtitle2}>
                            email&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;resume&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;linkedin
                        </Typography> */}
                        <div style={styles.links}>
                            <a href="mailto:paolo.velarde@mail.utoronto.ca" style={styles.subtitle2}>email</a>
                            <a href={resume} target="_blank" style={styles.subtitle2}>resume</a>
                            <a href="https://www.linkedin.com/in/paolo-mendez-velarde/" style={styles.subtitle2}>linkedin</a>
                        </div>
                    </Grid>
                    <Grid item xs={6} style={styles.pic}>
                        <img src={me} style={styles.me}/>
                    </Grid>
                </Grid>
                {/* <img onClick={this.test} src={chevronDown} style={styles.down}/> */}
            </div>
        )
    }
}

export default Home;